#main {
	width: 100%;
}

.site-content {
	min-height: 100vh;

	section.entry-content {
		> div:first-of-type {
	//		padding-top: 300px;
		}
	}
}
@include tablet-down {
	.secondary-column {
		margin-top: 4em;
	}
}



@include tablet-up{
	#inner-content {
		display: flex;

		> * {
			flex: 1;
		}

		> .main-column {
			min-width: $golden-large;
			padding: 2rem;
		}

		> .secondary-column {

		}
	}
}
